.modal{
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1000px;
    height: 600px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    background-color: #fff;
    position: relative;
    box-shadow: 24;
    padding: 2rem;
}